import React, {Component} from 'react';
import './App.css';
import fetch from 'node-fetch';


class App extends Component {
  constructor(props){
    super(props);
    this.state = {
      quotesArr: [{}],
      isLoaded: false,
      quote: {}

    };
    this.handleClick = this.handleClick.bind(this);
  }

  componentDidMount() {
    try{(async () => {
          const response = await fetch('https://type.fit/api/quotes');
          const data = await response.json();
          this.setState({quotesArr: data, isLoaded: true});
          this.setState({quote: this.state.quotesArr[Math.floor(Math.random()*this.state.quotesArr.length)]});
    })();
  } catch (error){
    this.setState({error: error.message})
  }
  }

  handleClick() {
    this.setState({quote: this.state.quotesArr[Math.floor(Math.random()*this.state.quotesArr.length)]})
  }

  handleTwitter(){

  }

  render() {
    const {error, quote} = this.state; 

    if (error){
      return <div>Error: {error.message}</div>
    } else {
      return (
      <div className="App">
        <div id="quote-box">
          <h1 id = 'heading'> &#8220; Random Quote Generator &#8220;</h1>
          <h2 id='text'>{quote.text}</h2>
          <h4 id = 'author'>{quote.author}</h4>
          <button id = 'new-quote' onClick = {this.handleClick}>New Quote</button>
          <a id = 'tweet-quote' href = 'https://www.twitter.com/intent/tweet'><img id = "tweet-icon" src = "./twitter.svg" ></img></a>
        </div>
      </div>
    )
    }
  }
}

export default App